import { useNavigation } from '@react-navigation/native';
import {Alert, Dimensions, Text, TouchableOpacity, View} from 'react-native';
import React from 'react';
import { useAppSelector } from '../../app/hooks';
import CalendarFunctions from './CalendarFunctions';
import {
  useGetCurrentSectionQuery,
  useRemoveClassMutation,
} from '../api/classPagesApi';

export default function renderItem(
  masterClassInfo,
  courseCode,
  handleClassSelected = (string: string) => {}
) {
  const calendarFun = new CalendarFunctions();
    const universalHeight = (Dimensions.get('window').height - 50) / 12
    const minuteHeight = universalHeight / 60
  const thisTime = masterClassInfo.times.find(
    (course) => course.courseCode === courseCode
  );

  const [startHour, startMinute] = thisTime.startTime.split(':');
  const [endHour, endMinute] = thisTime.endTime.split(':');
  let color = calendarFun.stringToColour(masterClassInfo.masterCourseCode);
  let code = courseCode.split(' ');
  return (
    <View
      key={thisTime._id}
      style={{
        backgroundColor: color,
        top: universalHeight * startHour + startMinute * minuteHeight,
        height:
          universalHeight * (endHour - startHour) +
          calendarFun.getMinutesDiff(startMinute, endMinute, minuteHeight),
        width: '100%',
        position: 'absolute',
        margin: 0,
        justifyContent: 'center',
      }}>
      <TouchableOpacity
        onPress={() => {
          handleClassSelected(masterClassInfo.masterCourseCode);
        }}
        onLongPress={
          () => {} /*{
                    Alert.alert(
                        courseCode,
                        null,
                        [
                            {
                                text: 'Delete',
                                onPress: () => {

                                },
                            },
                            {
                                text: 'Cancel',
                                onPress: () => console.log('Cancel Pressed'),
                                style: 'cancel',
                            },
                        ],
                        { cancelable: true }
                    );
                }*/
        }>
        <Text
          adjustsFontSizeToFit
          numberOfLines={5}
          style={{
            color: parseInt(color.slice(1), 16) > 9079434 ? 'black' : 'white',
            fontSize: 11,
            marginHorizontal: 3,
            textAlign: 'center',
          }}>
          {code[0]} {code[1]}
          {'\n'}
          {code[2]}
          {thisTime.classLocation ? '\n(' + thisTime.classLocation + ')' : null}
        </Text>
      </TouchableOpacity>
    </View>
  );
}
