import {
  Layout,
  Text,
  Card,
  Button,
  List,
  Input,
  useTheme,
  Spinner,
  Divider,
} from '@ui-kitten/components';
import { useEffect, useState } from 'react';
import {
  Keyboard,
  TouchableWithoutFeedback,
  ScrollView,
  TouchableOpacity,
  RefreshControl,
} from 'react-native';
import { FontAwesome5 } from '@expo/vector-icons';

import { selectDarkMode, selectUserKey } from '../../../app/appDataSlice';
import { useAppSelector } from '../../../app/hooks';
import DisabledFieldModal from '../../../components/DisabledOptionModal';
import {
  discoverApiSlice,
  postsAdapter,
  postsSelectors,
  useAddCommentMutation,
  useDeleteDiscoverPostMutation,
  useLazyGetPostByIdQuery,
  useLikePostMutation,
} from '../../api/discoverApi';
import { usersApiSlice } from '../../api/usersApi';
import BottomBar from '../BottomBar';
import Comment from './Comment';
import SinglePostBody from './SinglePostBody';
import { stringToEmoji } from '../../../functions';
import FullPageSpinner from '../../../components/FullPageSpinner';
import { useAddClassMutation } from '../../api/classPagesApi';
import { MAX_WIDTH_WEB } from '../../../constants/discover';
import ImageComponent from '../ImageComponent';
import {LinearGradient} from "expo-linear-gradient";

export default function SinglePost({
  route: {
    params: { _id, searchTerm, searchedTag },
  },
  navigation,
}) {
  const theme = useTheme();
  const darkMode = useAppSelector(selectDarkMode);
  const userKey = useAppSelector(selectUserKey);
  const [showModal, setShowModal] = useState(false);
  const [comment, setComment] = useState('');
  const [inputFocused, setInputFocused] = useState(false);

  const [likePost] = useLikePostMutation();
  const [refreshPost, { isLoading: refreshPostLoading }] =
    useLazyGetPostByIdQuery();
  const [addComment, { isLoading: addCommentLoading }] =
    useAddCommentMutation();
  const [deletePost, { isLoading: deletePostLoading }] =
    useDeleteDiscoverPostMutation();
  const { data: userData } =
    usersApiSlice.endpoints.getUserInfo.useQueryState(userKey);
  const post = discoverApiSlice.endpoints.getDiscoverPosts.useQueryState(
    { pageNumber: 1, userKey, userName: userData.userName },
    {
      selectFromResult: (result) =>
        postsSelectors.selectById(
          result.data ?? postsAdapter.getInitialState(),
          _id
        ),
    }
  );

  useEffect(() => {
    if (!post) navigation.goBack();
  }, [post]);

  const handleRefreshPost = async () => {
    try {
      await refreshPost({
        postId: _id,
        userKey,
        userName: userData.userName,
        searchTerm,
        tag: searchedTag,
      }).unwrap();
    } catch (error) {
      console.warn("Couldn't refresh post", error);
    }
  };

  const handleDeletePost = async () => {
    try {
      await deletePost({
        _id,
        userKey,
        userName: userData.userName,
        title: post.title,
        searchTerm,
        tag: searchedTag,
      }).unwrap();
    } catch (error) {
      console.warn("Couldn't delete post", error);
    }
  };

  const handleSubmitComment = async () => {
    try {
      await addComment({
        _id,
        userKey,
        userName: userData.userName,
        text: comment,
        searchTerm,
        tag: searchedTag,
      }).unwrap();
      setComment('');
      setInputFocused(false);
      Keyboard.dismiss();
    } catch (e) {
      console.warn('Error submitting comment', e);
    }
  };

  const handlePressLike = async () => {
    try {
      await likePost({
        userKey,
        _id,
        title: post.title,
        postAuthor: post.userName,
        userName: userData.userName,
        searchTerm,
        tag: searchedTag,
      }).unwrap();
    } catch (error) {
      console.log('Error liking post:', error);
    }
  };

  let body = <></>;
  if (post?.type === 'contentImage') {
    body = (
      <ImageComponent
        apiInfo={{
          userKey,
          _id,
          title: post.title,
          postAuthor: post.userName,
          userName: userData.userName,
          searchTerm,
          searchedTag,
        }}
        hasLiked={post.hasLiked}
        imageId={post.contentImage}
      />
    );
  } else if (post?.type === 'contentText' || post?.type === 'contentUrl') {
    body = (
      <SinglePostBody
        handleLikePost={handlePressLike}
        type={post.type}
        body={post.contentText || post.contentUrl}
      />
    );
  }

  const TopContent = () => {
    return (
      <Layout style={{ maxWidth: MAX_WIDTH_WEB, alignSelf: 'center' }}>
        <Card
          disabled
          style={{ margin: 8, borderWidth: 0 }}
          header={
            <Layout>
              <Text style={{ fontWeight: 'bold', fontSize: 20 }}>
                {post.title}
              </Text>
            </Layout>
          }
          footer={
            <Layout style={{ margin: 0, padding: 0 }}>
              <BottomBar
                commentIconShown={false}
                tags={post.tags}
                _id={post._id}
              />
            </Layout>
          }>
          {body}
        </Card>
        {post.userName === userData.userName ? (
          <Layout style={{ flexDirection: 'row', width: '100%' }}>
            <Button
              status="danger"
              style={{ margin: 8, flex: 1 }}
              onPress={handleDeletePost}>
              Delete Post
            </Button>
            {/* <Button style={{ margin: 8, flex: 1 }} disabled>
              Edit Post
            </Button> */}
          </Layout>
        ) : null}

        <Card disabled style={{ borderWidth: 0 }}>
          <Text
            style={{
              fontWeight: '600',
            }}>{`Comments (${post.comments.length})`}</Text>
        </Card>
      </Layout>
    );
  };

  if (!post) {
    return null;
  }

  return (
      <LinearGradient
          colors={
              darkMode
                  ? [theme['color-basic-1100'], theme['color-basic-1100']]
                  : [theme['color-primary-500'], theme['color-success-500']]
          }
          style={{ width: '100%', margin: 0, alignItems: 'center', flex: 1 }}
          start={{ x: 0, y: 0 }}
          end={{ x: 1, y: 0.4 }}>
    <Layout style={{ flex: 1 }}>
      {showModal && <DisabledFieldModal setShowModal={setShowModal} />}
      {/* Render Comments */}
      <List
        onRefresh={handleRefreshPost}
        showsVerticalScrollIndicator={false}
        refreshing={refreshPostLoading}
        ListHeaderComponent={TopContent}
        contentContainerStyle={{
          marginHorizontal: 10,
          maxWidth: 700,
          alignSelf: 'center',
        }}
        style={{ backgroundColor: 'transparent' }}
        data={post.comments}
        ItemSeparatorComponent={Divider}
        renderItem={({ item }) => (
          <Comment
            body={item.text}
            postId={post._id}
            commentId={item._id}
            numLikes={item.numLikes}
            hasLiked={item.hasLiked}
            userName={userData.userName}
            isOwnComment={item.userName === userData.userName}
            searchTerm={searchTerm}
            searchedTag={searchedTag}
          />
        )}
        keyExtractor={(comment) => {
          return comment._id;
        }}
      />
      <Layout
        style={{
          flexDirection: 'row',
          alignItems: 'center',
          height: 55,
          bottom: 0,
          width: '100%',
          paddingVertical: 10,
          paddingRight: 10,
          borderColor: darkMode
            ? theme['color-basic-500']
            : theme['color-basic-300'],
          borderTopWidth: 1,
        }}>
        <Layout
          style={{
            flex: 1,
            alignItems: 'center',
            justifyContent: 'center',
          }}>
          <Layout
            style={{
              borderRadius: 100,
              width: 35,
              height: 35,
              backgroundColor: darkMode
                ? theme['color-basic-600']
                : theme['color-basic-300'],
              justifyContent: 'center',
              alignItems: 'center',
            }}>
            <Text style={{ fontSize: 15 }}>
              {stringToEmoji(userData.userName)}
            </Text>
          </Layout>
        </Layout>

        <Input
          accessoryRight={
            inputFocused ? (
              <TouchableOpacity onPress={handleSubmitComment}>
                <FontAwesome5
                  name="arrow-alt-circle-right"
                  size={22}
                  color={theme['color-primary-500']}
                />
              </TouchableOpacity>
            ) : null
          }
          style={{ flex: 7 }}
          placeholder="Add a comment..."
          onFocus={() => setInputFocused(true)}
          onBlur={() => setInputFocused(false)}
          value={comment}
          onChangeText={(text) => setComment(text)}
          onSubmitEditing={handleSubmitComment}
        />
      </Layout>
      {(deletePostLoading || addCommentLoading) && <FullPageSpinner />}
    </Layout>
      </LinearGradient>
  );
}
