import React, {
  useCallback,
  useContext,
  useEffect,
  useRef,
  useState,
} from 'react';
import {
  Alert,
  Animated,
  Dimensions,
  Image,
  KeyboardAvoidingView,
  Platform,
  RefreshControl,
  SafeAreaView,
  ScrollView,
  StyleSheet,
  TextInput,
  TouchableOpacity,
  TouchableWithoutFeedback,
  View,
} from 'react-native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import BackButton from '../../components/BackButton';
import {
  IndexPath,
  Select,
  SelectItem,
  Layout,
  Text,
  Button,
  Input,
  List,
} from '@ui-kitten/components';
import { base_URL } from './constants';
import { useNavigation } from '@react-navigation/native';
import { FontAwesome5, FontAwesome } from '@expo/vector-icons';
import {useAppDispatch, useAppSelector} from '../../app/hooks';
import {selectDarkMode, selectIsGuest, selectUserKey} from '../../app/appDataSlice';
import { useGetCurrentSectionQuery } from '../api/classPagesApi';
import CalendarFunctions from '../calendar/CalendarFunctions';
import { useGetUserInfoQuery } from '../api/usersApi';
import {classSelected, selectClassesList} from '../classPages/classPagesSlice';
import renderCustomEvent from '../calendar/customEvent';
import renderItem from '../calendar/ScheduledItem';
import GestureRecognizer from 'react-native-swipe-gestures';
import { SwipeRow } from 'react-native-swipe-list-view';
import BottomSheet from "@gorhom/bottom-sheet/lib/typescript/components/bottomSheet/BottomSheet";
import ModalContainer from "../../components/ModalContainer";
import UserNameSelectionDropDown from "../../components/UserNameSelectionDropDown";
import {Class} from "../../app/types";
import SingleClassPage from "./notificationSinglePage";

const NotificationHomePage = createNativeStackNavigator();

export default function NotificationsNavigation({ navigation }) {
  const [notifications, setNotifications] = useState([]);
  const [selectedIndex, setSelectedIndex] = React.useState([]);
  const [hasChosen, setHasChosen] = useState(false);
  const [users, setUsers] = useState([]);
  const [showGuestModal, setShowGuestModal] = useState(false)
  const [isOpen, setIsOpen] = useState(false);
  const userKey = useAppSelector(selectUserKey);
  const darkMode = useAppSelector(selectDarkMode);
  const isGuest = useAppSelector(selectIsGuest)

  useEffect(() => {
    refreshNotifications();
    fetch(base_URL + '/users/getUserNameList', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        userKey: userKey,
      },
    }).then(async (data) => {
      if (data.ok) {
        let newUsers = (await data.json()).map((userName) => {
          return {
            label: userName,
            id: userName,
          };
        });
        setUsers(newUsers);
      }
    });
  }, []);
  async function refreshNotifications() {
    fetch(base_URL + '/users/getNotifications', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        userKey: userKey,
      },
    }).then((res) => {
      res.text().then((notifications) => {
        notifications = JSON.parse(notifications);
        // @ts-ignore
        setNotifications(notifications);
      });
    });
  }
  async function sendNotification(bodyTosend) {
    await fetch(base_URL + '/users/sendNotification', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        userKey: userKey,
      },
      body: JSON.stringify(bodyTosend),
    });
  }
  function createNotificationButton() {
    navigation.navigate('CreateNotification');
  }
  function clearNotifications() {
    fetch(base_URL + '/users/clearNotifications', {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
        userKey: userKey,
      },
    }).then(() => {
      refreshNotifications();
    });
  }
  const navigationHeader = (navigation) => (
    <Layout
      style={{
        width: '100%',
        justifyContent: 'space-between',
        flexDirection: 'row',
        alignItems: 'center',
        flex: 1,
        backgroundColor: 'transparent',
      }}>
      {Platform.OS === 'ios' && <BackButton navigation={navigation} />}

      <Layout
        style={{
          paddingBottom: 5,
          flexDirection: 'row',
          alignItems: 'center',
          justifyContent: 'center',
          flex: 1,
          backgroundColor: 'transparent',
        }}>
        {/* <Image
          style={{
            height: 28,
            width: 28,
          }}
          source={require('../../../assets/logos/app-logo.png')}
        /> */}
        <Text
          style={{
            paddingTop: 3,
            fontSize: 20,
            fontWeight: '500',
            color: darkMode ? '#fff' : '#000',
          }}>
          Notifications
        </Text>
      </Layout>
      <Layout
        style={{
          alignSelf: 'flex-end',
          paddingRight: 40,
          paddingBottom: 10,
          flexDirection: 'row',
          backgroundColor: 'transparent',
        }}>
        <TouchableOpacity
          style={{
            paddingLeft: 20,
            marginRight: Platform.OS == 'android' ? 50 : 0,
          }}
          onPress={() => {
            if (isGuest) {
              setShowGuestModal(true)
            } else {
              createNotificationButton();
            }
          }}>
          <FontAwesome5 size={25} color={'#1E90FF'} name="plus" />
        </TouchableOpacity>
        {
            showGuestModal &&
            <ModalContainer
                title="Wait!"
                children={<Text style={{paddingHorizontal: 30}}>This feature is locked to our Guest Users. Please create a free account with us to take advantage this feature.</Text>}
                backDropVisible={showGuestModal}
                setBackDropVisible={setShowGuestModal}
            />
        }
        {/*<TouchableOpacity

          onPress={() => {
            Alert.alert(
              'Caution',
              'Are you sure you want to clear your notifications?\nThis action cannot be undone.',
              [
                {
                  text: 'No',
                  style: 'cancel',
                },
                {
                  text: 'Yes',
                  onPress: async () => {
                    clearNotifications();
                  },
                },
              ]
            );
          }}>
          <FontAwesome5 size={25} color={'#d71717'} name="times" />
        </TouchableOpacity>
        /* <TouchableOpacity
          style={{ paddingLeft: 20 }}
          onPress={() => {
            refreshNotifications();
          }}>
          <FontAwesome size={25} color={'#1E90FF'} name="rotate-right" />
        </TouchableOpacity> */}
      </Layout>
    </Layout>
  );
  const Notifications = ({ navigation }) => {
    const [refreshing, setRefreshing] = useState(false);
    const [isMoving, setIsMoving] = useState(false);

    const onRefresh = useCallback(() => {
      setRefreshing(true);
      refreshNotifications().then(() => {
        setRefreshing(false);
      });
    }, []);
    async function handleOnLeftSwipe(postId) {
      const newNotifications = notifications.map((not) => {
        if (not._id !== postId) {
          return not;
        }
      });
      console.log(newNotifications);
      setNotifications(newNotifications);
      let res = await fetch(base_URL + '/users/removeNotification', {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
          userKey: userKey,
        },
        body: JSON.stringify({
          id: postId,
        }),
        redirect: 'follow',
      });
      if (res.ok) {
        refreshNotifications();
      } else {
        console.log(await res.json());
      }
      setIsOpen(false);
      setIsMoving(false);
    }

    const renderItem = ({ item: notification }) => {
      return (
        <Layout>
          {notification && (
            // @ts-ignore
            <SwipeRow
              disableRightSwipe={true}
              onSwipeValueChange={(status) => {
                if (status.value < -Dimensions.get('window').width * (1/3)) {
                  if (!isOpen) {
                    setIsOpen(true);
                    handleOnLeftSwipe(notification._id);
                  }
                } else if (status.value < -5) {
                  setIsMoving(true);
                } else {
                  setIsMoving(false);
                }
              }}
              useNativeDriver={true}>
              <Layout
                style={{
                  alignItems: 'flex-end',
                  justifyContent: 'center',
                  alignSelf: 'center',
                  width: '95%',
                  height: 75,
                  borderRadius: 11,
                  backgroundColor: '#ce0f0f',
                  marginLeft: 5,
                  paddingRight: 20,
                  marginRight: 5,
                  marginTop: 10,
                }}>
                <Text style={{ color: '#fff' }}>Delete</Text>
              </Layout>
              <TouchableWithoutFeedback
                style={{
                  alignItems: 'center',
                  alignSelf: 'center',
                  width: '95%',
                  height: 75,
                  borderRadius: 10,
                  marginLeft: 5,
                  marginRight: 5,
                  marginTop: 10,
                }}
                onPress={() => {
                  navigation.navigate('SingleNotification', {
                    data: notification,
                  });
                }}
                key={notification?._id}>
                <Layout
                  style={{
                    alignItems: 'center',
                    alignSelf: 'center',
                    width: '95%',
                    height: 75,
                    borderRadius: 10,
                    backgroundColor: darkMode ? '#333333' : '#ebebeb',
                    shadowColor: '#000',
                    shadowOffset: { width: 0, height: 1 },
                    shadowOpacity: 0.2,
                    shadowRadius: 2,
                    elevation: 1,
                    marginLeft: 5,
                    marginRight: 5,
                    marginTop: 10,
                  }}>
                  <Text
                    style={{
                      paddingTop: 5,
                      fontWeight: 'bold',
                      fontSize: 20,
                    }}>
                    {notification?.title}
                  </Text>
                  <Text style={{}}>From: {notification?.from}</Text>
                  {notification?.creationDate ? (
                    <Text style={{ fontStyle: 'italic', fontSize: 12 }}>
                      {notification?.creationDate}
                    </Text>
                  ) : null}
                </Layout>
              </TouchableWithoutFeedback>
            </SwipeRow>
          )}
        </Layout>
      );
    };

    return (
      <Layout style={{ flex: 1 }}>
        {notifications.length === 0 ? (
          <Layout style={{ alignItems: 'center' }}>
            <Text
              style={{
                paddingTop: '25%',
                paddingBottom: 10,
                textAlign: 'center',
              }}>
              No New Notifications
            </Text>
            <TouchableOpacity
              onPress={() => {
                refreshNotifications();
              }}>
              <FontAwesome size={25} color={'#1E90FF'} name="rotate-right" />
            </TouchableOpacity>
          </Layout>
        ) : null}
        <List
          style={{ backgroundColor: 'transparent' }}
          contentContainerStyle={{ flex: 1 }}
          data={notifications}
          scrollEnabled={!isMoving}
          renderItem={renderItem}
          onRefresh={refreshNotifications}
          refreshing={refreshing}
        />
      </Layout>
    );
  };

  function getDateText(text) {
    let spaceIndex = text.indexOf(' ');
    let first = text.substring(0, spaceIndex);
    let second = text.substring(spaceIndex + 1);
    return second + ' |  ' + first;
  }

  const SingleNotification = ({
    route: {
      params: { data },
    },
  }) => {
    navigation = useNavigation();
    const { data: currentSection } = useGetCurrentSectionQuery();

    let listToRemove = [];
    if (data.isSchedule) {
      let jsonSchedule = JSON.parse(data.message);
      let thereIsAtLeastOne = false;
      for (const index in jsonSchedule) {
        if (jsonSchedule[index].courseTerm === currentSection) {
          thereIsAtLeastOne = true;
        } else {
          listToRemove.push(index);
        }
      }
      for (const index of listToRemove) {
        jsonSchedule.splice(index, 1);
      }
      if (!thereIsAtLeastOne) {
        Alert.alert(
          'This schedule is either empty or contains outdated class information'
        );
        navigation.goBack();
      }
      return thereIsAtLeastOne ? (
        <Layout>{Calendar(jsonSchedule)}</Layout>
      ) : null;
    } else if (data.isCourseData) {
      const allClassData = useAppSelector(selectClassesList);
      const courseSimpleData = JSON.parse(data.message);
      if (courseSimpleData.courseTerm === currentSection) {
        return <SingleClassPage
            selectedClass={allClassData[courseSimpleData.masterCourseCode]}/>
      } else {
        Alert.alert(
            'This course contains outdated information'
        );
        navigation.goBack();
        return null
      }
    }
    return (
      <KeyboardAvoidingView
        style={{ flex: 1 }}
        behavior={Platform.OS === 'ios' ? 'padding' : 'height'}>
        <ScrollView contentContainerStyle={{ flex: 1 }}>
          <Layout style={sponsorPageStyles.container}>
            <Layout
              style={{ width: '100%', alignItems: 'center', marginTop: 20 }}>
              <Text style={sponsorPageStyles.title}>{data.title}</Text>
            </Layout>

            {data.from && (
              <Layout style={sponsorPageStyles.address}>
                <Layout style={{ flexDirection: 'row' }}>
                  <Text style={{ fontSize: 15, fontWeight: 'bold' }}>
                    From:{' '}
                  </Text>
                  <Text style={{ fontSize: 15 }}>{data.from}</Text>
                </Layout>
                <Layout style={{ flexDirection: 'row' }}>
                  <Text style={{ fontSize: 15 }}>
                    {getDateText(data.creationDate)}
                  </Text>
                </Layout>
              </Layout>
            )}
            <Layout style={sponsorPageStyles.description}>
              <Text>{data.message}</Text>
            </Layout>
            {data.repliable && (
              <TouchableOpacity
                onPress={() => {
                  // TODO: reply
                }}
                style={myStyles.button}>
                <Text style={myStyles.text}>Reply</Text>
              </TouchableOpacity>
            )}
          </Layout>
        </ScrollView>
      </KeyboardAvoidingView>
    );
  };
  const CreateNotification = ({ navigation }) => {
    const [body, setBody] = useState({});
    const [done, setDone] = useState(false);
    const [recipient, setRecipient] = useState(null)
    const [hasChosen, setHasChosen] = useState(false);

    useEffect(() => {
      changeValue(hasChosen ? [recipient] : null, 'recipients')
    }, [recipient, hasChosen])

    function checkForDone(tempBody) {
      if (tempBody.recipients && tempBody.title && tempBody.text) {
        setDone(true);
      } else {
        setDone(false);
      }
    }

    const changeValue = (theValue, valueToChange) => {
      let newBody = body;
      for (const aspect in newBody) {
        if (
            newBody[aspect] === null ||
            newBody[aspect] === undefined ||
            newBody[aspect] === ''
        ) {
          delete newBody[aspect];
        }
      }
      if (theValue !== undefined) {
        newBody[valueToChange] = theValue;
      } else {
        if (newBody[valueToChange]) {
          delete newBody[valueToChange];
        }
      }
      console.log(JSON.stringify(newBody));
      checkForDone(newBody);
      setBody(newBody);
    };

    function renderInputFields() {
      const renderHeader = () => {
        return (
          <Layout style={{ flexDirection: 'row' }}>
            <Text
              style={{
                fontSize: 15,
                paddingLeft: 12,
                paddingBottom: 5,
                paddingTop: 5,
                alignContent: 'center',
                fontWeight: 'bold',
              }}>
              Send Message
            </Text>
          </Layout>
        );
      };
      let final = [];
      final.push(
          <View style={{
            height: 50,
            margin: 12,
            padding: 10,
          }}>
            {UserNameSelectionDropDown({setHasChosen, setRecipient, placeholder: true})}
          </View>
      );
      final.push(
        <Input
          autoCapitalize={'words'}
          onChangeText={(theValue) => changeValue(theValue, 'title')}
          style={myStyles.input}
          placeholderTextColor={'gray'}
          placeholder={'Title'}
        />
      );
      final.push(
        <Input
          autoCapitalize={'sentences'}
          onChangeText={(theValue) => changeValue(theValue, 'text')}
          style={myStyles.inputBig}
          multiline={true}
          textStyle={{ minHeight: 150 }}
          placeholderTextColor={'gray'}
          placeholder={'Body'}
        />
      );

      return (
        <Layout style={{ flex: 1 }}>
          {renderHeader()}
          {final}
        </Layout>
      );
    }
    return (
      <SafeAreaView style={{ flex: 1 }}>
        <KeyboardAvoidingView
          style={{ flex: 1 }}
          behavior={Platform.OS === 'ios' ? 'padding' : 'height'}>
          <ScrollView contentContainerStyle={{ flex: 1 }}>
            <Layout style={sponsorPageStyles.container}>
              <Layout style={sponsorPageStyles.description}>
                {renderInputFields()}
                <Button
                  disabled={!done}
                  onPress={async () => {
                    // console.log(JSON.stringify(body));
                    await sendNotification(body);
                    navigation.navigate('Notifications');
                  }}>
                  <Text style={myStyles.text}>Send Message</Text>
                </Button>
              </Layout>
            </Layout>
          </ScrollView>
        </KeyboardAvoidingView>
      </SafeAreaView>
    );
  };
  return (
    <NotificationHomePage.Navigator initialRouteName="Notifications & Messages">
      <NotificationHomePage.Screen
        options={({ navigation }) => ({
          title: 'Notifications',
          headerTitle: () => {
            return navigationHeader(navigation);
          },
        })}
        name="Notifications"
        component={Notifications}
      />
      <NotificationHomePage.Screen
        name="SingleNotification"
        component={SingleNotification}
        options={{ headerTitle: '' }}
      />
      <NotificationHomePage.Screen
        name="CreateNotification"
        component={CreateNotification}
        options={{ headerTitle: '' }}
      />
    </NotificationHomePage.Navigator>
  );
}

function convertTo12HourTime(time) {
  if (time.length !== 5) {
    time = '0' + time;
  }
  let hours = time.slice(0, 2);
  let minutes = time.slice(3, 5);
  let ampm = Number.parseInt(hours) > 12 ? 'pm' : 'am';
  hours = hours % 12;
  if (hours === 0) {
    hours = 12;
    ampm = 'pm';
  }
  return hours + ':' + minutes + ampm;
}

function getMinutesDiff(startTime, endTime) {
  if (endTime > startTime) {
    return (endTime - startTime) * 0.7;
  } else if (startTime === endTime) {
    return 0;
  } else {
    return (startTime - endTime) * -0.7;
  }
}

function Calendar(userData) {
  const allClassData = useAppSelector(selectClassesList);
  const { data: currentSection, isLoading: isCurrentSectionLoading } =
    useGetCurrentSectionQuery();
  const darkMode: Boolean = useAppSelector(selectDarkMode);
  const calendarFun = new CalendarFunctions();
  const days = [
    { day: 'Mon', id: 'Monday' },
    { day: 'Tues', id: 'Tuesday' },
    { day: 'Wed', id: 'Wednesday' },
    { day: 'Thurs', id: 'Thursday' },
    { day: 'Fri', id: 'Friday' },
    { day: 'Sat', id: 'Saturday' },
    { day: 'Sun', id: 'Sunday' },
  ];
  const times = [
    '12:00 AM',
    '1:00 AM',
    '2:00 AM',
    '3:00 AM',
    '4:00 AM',
    '5:00 AM',
    '6:00 AM',
    '7:00 AM',
    '8:00 AM',
    '9:00 AM',
    '10:00 AM',
    '11:00 AM',
    '12:00 PM',
    '1:00 PM',
    '2:00 PM',
    '3:00 PM',
    '4:00 PM',
    '5:00 PM',
    '6:00 PM',
    '7:00 PM',
    '8:00 PM',
    '9:00 PM',
    '10:00 PM',
    '11:00 PM',
  ];

  function renderDashes() {
    let final = [];
    for (let i = 0; i < 24; i++) {
      final.push(
        <Layout
          style={{
            width: 46,
            marginLeft: 9,
            top: 41.7 * i,
            position: 'absolute',
            height: 41.7,
            borderBottomWidth: 0.7,
            borderBottomColor: darkMode ? 'rgb(68,68,68)' : 'rgb(204,204,204)',
          }}
        />
      );
    }
    return final;
  }

  const renderClasses = (elem) => {
    let classes = [];
    for (const course of userData) {
      if (course.courseTerm === currentSection) {
        let data = calendarFun.getFullClassData(
          course.courseCode,
          allClassData
        );
        let data2 = data.times.find(
          (time) => time.courseCode === course.courseCode
        );
        for (const day in data2['daysOfTheWeek']) {
          if (data2['daysOfTheWeek'][day] === elem.id) {
            classes.push(renderItem(data, course.courseCode, () => {}));
          }
        }
      }
    }

    return classes;
  };

  const topBar = useRef(null);

  return (
    <SafeAreaView
      style={{
        marginTop: 0,
        marginLeft: 0,
        paddingBottom: 15,
        marginBottom: 20,
      }}>
      {/* Times */}
      <ScrollView
        horizontal={true}
        snapToInterval={63}
        showsHorizontalScrollIndicator={false}
        style={{ marginLeft: 75 }}
        ref={topBar}>
        <View style={{ flexDirection: 'row' }}>
          {days.map((elem) => {
            return (
              <Layout>
                <Layout
                  style={{
                    borderColor: darkMode ? '#7a7a7a' : '#525252',
                    borderBottomWidth: 1,
                    width: '100%',
                  }}>
                  <Text
                    style={{
                      textAlign: 'center',
                      color: darkMode ? '#7a7a7a' : '#525252',
                      width: 63,
                    }}>
                    {elem.day}
                  </Text>
                </Layout>
              </Layout>
            );
          })}
        </View>
      </ScrollView>
      <ScrollView
        contentOffset={{ x: 0, y: 292 }}
        showsVerticalScrollIndicator={false}
        bounces={false}>
        <View style={{ flexDirection: 'row' }}>
          <View
            style={{
              justifyContent: 'space-around',
              height: 1000,
            }}>
            {times.map((time) => {
              return (
                <Text
                  style={{
                    textAlign: 'center',
                    color: darkMode ? '#7a7a7a' : '#525252',
                    paddingTop: '15%',
                    height: 43,
                    width: 75,
                    borderWidth: 1,
                    borderColor: darkMode ? '#7a7a7a' : '#525252',
                  }}>
                  {time}
                </Text>
              );
            })}
          </View>
          {/* Classes */}
          <ScrollView
            horizontal={true}
            snapToInterval={63}
            decelerationRate={'normal'}
            showsHorizontalScrollIndicator={false}
            bounces={false}
            scrollEventThrottle={1}
            onScroll={(e) =>
              topBar.current.scrollTo({
                x: e.nativeEvent.contentOffset.x,
                animated: false,
                useNativeDriver: true,
              })
            }
            onMomentumScrollBegin={(e) =>
              topBar.current.scrollTo({
                x: e.nativeEvent.contentOffset.x,
                animated: false,
                useNativeDriver: true,
              })
            }>
            <View style={{ flexDirection: 'row' }}>
              {days.map((elem) => {
                return (
                  <Layout>
                    {renderDashes()}
                    <Layout key={elem.day} style={{ width: 63 }}>
                      {renderClasses(elem)}
                    </Layout>
                    <Layout
                      style={{
                        height: '100%',
                        width: 1,
                        backgroundColor: darkMode ? '#7a7a7a' : '#525252',
                      }}
                    />
                  </Layout>
                );
              })}
            </View>
          </ScrollView>
        </View>
      </ScrollView>
    </SafeAreaView>
  );
}

