import React, { useEffect, useState } from 'react';
import { Image, Platform, TouchableOpacity } from 'react-native';
import { FontAwesome5, Ionicons } from '@expo/vector-icons';
import { Button, Layout, Text, useTheme } from '@ui-kitten/components';

import ClassesList from './ClassesList';
import { SponsorsList, SponsorPage } from '../sponsors/SponsorsList';
import Header from '../../components/Header';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import Settings from '../settings/index';
import { useAppSelector } from '../../app/hooks';
import {
  selectDarkMode,
  selectIsGuest,
  selectUserKey,
} from '../../app/appDataSlice';
import NotificationsNavigation from '../notifications/Notifications';
import { base_URL } from '../notifications/constants';
import SingleClassPage from './SingleClassPage/index';
import SelectTerm from './SelectTerm';

const Stack = createNativeStackNavigator();

export default function SearchClasses({ navigation }) {
  const theme = useTheme();
  const userKey = useAppSelector(selectUserKey);
  const darkMode = useAppSelector(selectDarkMode);
  const [numberOfNotifications, setNumberOfNotifications] = useState(0);

  function refreshNotifications() {
    fetch(base_URL + '/users/getNotifications', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        userKey: userKey,
      },
    }).then((res) => {
      res.text().then((notifications) => {
        notifications = JSON.parse(notifications);
        setNumberOfNotifications(notifications.length);
      });
    });
  }

  useEffect(() => {
    refreshNotifications();
    setInterval(() => {
      refreshNotifications();
    }, 60000); // refresh every minute
  }, []);

  const accessoryLeft = (
    <Image
      style={{
        height: 28,
        width: 28,
      }}
      source={require('../../../assets/logos/app-logo-transparent.png')}
    />
  );

  const accessoryRight = (
    <Layout
      style={{
        flexDirection: 'row',
        width: 100,
        justifyContent: 'space-between',
      }}>
      <TouchableOpacity
        onPress={() => {
          navigation.navigate('SponsorsList');
        }}
        style={{ flexDirection: 'row', alignItems: 'center' }}>
        <FontAwesome5
          name="shopping-bag"
          size={23}
          color={darkMode ? theme['color-basic-200'] : theme['color-basic-800']}
        />
      </TouchableOpacity>
      <TouchableOpacity
        onPress={() => {
          navigation.navigate('Settings');
        }}>
        <Ionicons
          name="settings"
          size={23}
          color={darkMode ? theme['color-basic-200'] : theme['color-basic-800']}
        />
      </TouchableOpacity>
      <TouchableOpacity
        onPress={() => {
          navigation.navigate('NotificationsPage');
        }}>
        <FontAwesome5
          name="bell"
          size={23}
          color={darkMode ? theme['color-basic-200'] : theme['color-basic-800']}
        />
        {numberOfNotifications > 0 ? (
          <Layout
            style={{
              position: 'absolute',
              backgroundColor: darkMode ? 'red' : '#fff',
              borderRadius: 10,
              paddingHorizontal: 3,
              right: -7,
              top: -3,
            }}>
            <Text>{numberOfNotifications}</Text>
          </Layout>
        ) : null}
      </TouchableOpacity>
    </Layout>
  );

  return (
    <Stack.Navigator>
      <Stack.Screen
        name="ClassesList"
        component={ClassesList}
        options={{
          header: () => (
            <Layout style={{ paddingBottom: 10 }}>
              <Header
                accessoryLeft={accessoryLeft}
                accessoryRight={accessoryRight}
                title="Athena"
              />
            </Layout>
          ),
          title: 'ClassPages',
        }}
      />
      <Stack.Screen
        name="Settings"
        component={Settings}
        options={{
          headerShown: false,
        }}
      />
      <Stack.Group screenOptions={{ headerTitle: 'Sponsors' }}>
        <Stack.Screen
          name="SponsorsList"
          component={SponsorsList}
          options={{ title: 'Sponsors' }}
        />
        <Stack.Screen
          name="SponsorPage"
          component={SponsorPage}
          options={{ title: 'Sponsors' }}
        />
      </Stack.Group>
      <Stack.Screen
        name="NotificationsPage"
        component={NotificationsNavigation}
        options={{ headerShown: false, title: 'Notifications' }}
      />
      <Stack.Screen
        name="SelectTerm"
        component={SelectTerm}
        options={{
          title: 'Select Term',
          headerShown: false,
          animation: 'slide_from_bottom',
          gestureDirection: 'vertical',
        }}
      />
      {Platform.OS === 'web' && (
        <Stack.Screen
          name="SingleClassPage"
          component={SingleClassPage}
          options={{
            headerShown: false,
            title: 'ClassPages',
          }}
        />
      )}
    </Stack.Navigator>
  );
}
